.header__logo {
    display: block;
    width: 103.74px;
    margin-left: 27px;
    margin-bottom: 31.6px;
}
@media screen and (min-width: 768px) {
    .header__logo {
        width: 120px;
        margin-left: 20px;
        margin-bottom: 35px;
    }
}
@media screen and (min-width: 1024px) {
    .header__logo {
        width: 142px;
        margin-left: 10px;
        margin-bottom: 38px;
    }
}
@media screen and (min-width: 1280px) {
    .header__logo {
        margin-left: 0;
        margin-bottom: 41px;
    }
}
